import {
	get,
	post
} from '../../libs/request.js';
export default {
	data() {
		return {
			checked: [1],
			tips: "",
			sn: "",
			orders: null
		};
	},
	onLoad(e) {
		this.sn = e.sn;
		this.getOrderTips(this.sn);
	},
	onShow() {
		this.getUserOrder({
			leaseStatus: 2
		});
	},
	methods: {
		changed(e) {
			this.checked = e.detail.value;
		},
		toAgreement() {
			uni.navigateTo({
				url: `agreement`,
			});
		},
		getUserOrder(data) {
			// 获取用户订单
			post('order/list', data).then(res => {
				if (res.code === 200) {
					this.orders = res.data;
				}
			});
		},
		submit(e) {
			let sn = this.sn;

			let formId = e.detail.formId || "";

			if (this.checked.length > 0) {
				if (this.orders && this.orders.length > 0) {
					return uni.showToast({
						title: "存在租借中的订单",
						icon: "none"
					})
				} else {




					this.getAliPay(sn,formId);

				}
			} else {
				uni.showToast({
					title: '请勾选协议'
				});
			}
		},
		getWechart(sn) {
			let _this = this;
			wx.requestSubscribeMessage({
				tmplIds: ['mooj8JXCS7QA313DTeFdMLyo-KDpDfqVbv7z5_fix28', 'H0iqZpWig24QyDJYe-F4OaAGnWvEumBrY8glxtV8QcU'],
				success(res) {
					console.log('消息', res)
				},
				fail(err) {
					console.log('消息', err)
				},
				complete() {
					post('wxpay/payscore', {
						sn: sn
					}).then(res => {
						console.info('step1', res.data);
						if (res.code === 200) {
							wx.openBusinessView({
								businessType: 'wxpayScoreUse',
								extraData: {
									mch_id: res.data.mchId,
									package: res.data.packageInfo,
									timestamp: res.data.timestamp,
									nonce_str: res.data.nonceStr,
									sign_type: res.data.signType,
									sign: res.data.sign
								},
								success() {
									//dosomething
									console.info('step2')
									wx.navigateToMiniProgram({
										appId: 'wxd8f3793ea3b935b8',
										path: 'pages/use/use',
										extraData: {
											mch_id: res.data.mchId,
											package: res.data.packageInfo,
											timestamp: res.data.timestamp,
											nonce_str: res.data.nonceStr,
											sign_type: res.data.signType,
											sign: res.data.sign
										},
										success() {
											uni.navigateTo({
												url: `loaning?orderNum=${res.data.orderNum}`,
											});
										},
										fail(result) {},
										complete() {
											//dosomething
											uni.navigateTo({
												url: `loaning?orderNum=${res.data.orderNum}`,
											});
										}
									});
								},
								fail(err) {
									//dosomething
									uni.showModal({
										title: '提示',
										content: '获取免押失败，是否交押金使用？',
										confirmText: "确定",
										cancelText: "取消",
										success: function(res) {
											if (res.confirm) {
												_this.payWechart(sn);
											} else if (res.cancel) {
												uni.navigateTo({
													url: "index"
												});
											}
										}
									});

								},
								complete() {
									//dosomething
								}
							});
						}
					});
				}
			})


		},
		getAliPay(sn,formId) {
			post('/wxpay/freeze', {
				sn: sn,
				formId:formId
			}).then(res => {
				if (res.code === 200) {

					console.log('orderStr', res.data.orderStr)
					my.tradePay({
						// 调用统一收单交易创建接口（alipay.trade.create），获得返回字段支付宝交易号trade_no
						orderStr: res.data.orderStr,
						success: (rs) => {
							if (Number(rs.resultCode) === 9000) {
								uni.navigateTo({
									url: `loaning?orderNum=${res.data.orderNum}`,
								});
							} else {
								uni.showToast({
									title: '支付失败'
								})
							}
						},
						fail: (res) => {
							console.log('授权失败', res)
							uni.showToast({
								title: '授权失败'
							})
							uni.navigateTo({
								url: "index"
							});
						}
					});

				}
			});
		},
		payWechart(sn) {
			post('/wxpay/prepay', {
				sn: sn
			}).then(res => {
				wx.requestPayment({
					timeStamp: res.data.timeStamp,
					nonceStr: res.data.nonceStr,
					package: res.data.package,
					signType: res.data.signType,
					paySign: res.data.paySign,
					success() {
						uni.navigateTo({
							url: `loaning?orderNum=${res.data.orderNum}`,
						});
					},
					fail(res) {
						uni.navigateTo({
							url: "index"
						});
					}
				})
			})
		},
		getOrderTips(sn) {
			post('/order/bill', {
				sn: sn
			}).then(res => {
				this.tips = res.data;
			})

		}
	}
};
